import React, { Component } from "react";
import BootstrapTable from "react-bootstrap-table-next";
import ToolkitProvider, {
  Search,
  CSVExport,
} from "react-bootstrap-table2-toolkit";

import FormCustomer from "./Component/FormCustomer";
import { connect } from "react-redux";
import { editcustomer, getcustomerList } from "../actions/customerAction";
import notrespond from "../Images/500.svg";
import Swal from "sweetalert2";
import FormCustomerEdit from "./Component/FormCustomerEdit";
import { reset } from "redux-form";
import Skeleton from "react-loading-skeleton";
import paginationFactory from "react-bootstrap-table2-paginator";
import { berhasil, gagal, LogData, reactive, warning } from "../Alert";
import { AxiosMasterDelete, AxiosMasterPost, AxiosMasterPut } from "../Axios";
import { hideModal, showModal } from "../actions/ModalAction";
import ModalMasterData from "./Component/ModalMasterData";
import HeaderCard from "../HeaderCard";
import TourGuide from "../TourGuide";
import { finishSend, progressSend } from "../actions/LogicAction";

const { SearchBar } = Search;
const { ExportCSVButton } = CSVExport;

function hapus(dispatch, kode) {
  Swal.fire({
    title: "Anda Yakin ?",
    text: "Anda Akan Menghapus Data Ini ?",
    icon: "warning",
    showCancelButton: true,
    confirmButtonColor: "#3085d6",
    cancelButtonColor: "#d33",
    confirmButtonText: "Yes, delete it!",
  }).then((result) => {
    if (result.isConfirmed) {
      let data = {
        data: {
          kodeCustomer: kode,
        },
      };
      AxiosMasterDelete("customer/delete/" + kode)
        .then((res) =>
          berhasil("Data Berhasil Dihapus!")
            .then(() => LogData("Data Berhasil Dihapus Di Customer", data))
            .then(() => dispatch(getcustomerList()))
        )
        .catch((err) =>
          gagal("Data Gagal Dihapus!").then(() =>
            LogData("Data Gagal Dihapus Di Customer", data, err)
          )
        );
    }
  });
}

const rowExpand = {
  renderer: (row) => (
    <div>
      <p>{`Nama Kota: ${row.kode_kota}`}</p>
      <p>{`Email: ${row.email == null ? "-" : row.email} | Alamat : ${
        row.alamat == null ? "-" : row.alamat
      } | Kode Pos : ${row.kode_pos == null ? "-" : row.kode_pos} | Telepon : ${
        row.no_telp == null ? "-" : row.no_telp
      }| Handphone: ${row.no_hp == null ? "-" : row.no_hp}`}</p>
      <p>{`Kode Ext : ${row.kode_ext == null ? "-" : row.kode_ext}`}</p>
    </div>
  ),
};

const maptoStae = (state) => {
  return {
    customerList: state.provinsi.customer,
    alert: state.provinsi.alert,
    error: state.provinsi.error,
  };
};

class Customer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      Customer: [],
      response: "",
      columns: [
        {
          dataField: "nama_customer",
          text: "Nama Customer",
        },
        {
          dataField: "tanggal_daftar",
          text: "Tanggal Daftar",
        },
        {
          dataField: "nama_toko",
          text: "Nama Toko",
        },
        {
          dataField: "level_customer",
          text: "Level Customer",
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row) => {
            return (
              <div className="row">
                <button
                  className="btn btn-danger mr-2"
                  onClick={() => hapus(this.props.dispatch, row.kode_customer)}
                  data-tut="reactour_3"
                >
                  <i className="fa fa-trash mr-1"></i>
                  HAPUS
                </button>
                <button
                  data-tut="reactour_4"
                  className="btn btn-warning"
                  onClick={() =>
                    this.props.dispatch(
                      editcustomer(
                        row.alamat,
                        row.email,
                        row.no_hp,
                        row.kode_kecamatan,
                        row.kode_customer,
                        row.kode_ext,
                        row.kode_kota,
                        row.kode_pos,
                        row.nama_customer,
                        row.nama_toko,
                        row.tanggalDaftar,
                        row.no_telp,
                        row.kode_provinsi,
                        row.fax,
                        row.contact_person,
                        this.editModal()
                      )
                    )
                  }
                >
                  <i className="fa fa-edit mr-2"></i>
                  EDIT
                </button>
              </div>
            );
          },
        },
      ],
    };
  }

  componentDidMount() {
    this.props.dispatch(getcustomerList());
  }

  updateStatus = (input) => {
    console.log(input);
    this.setState({
      response: input,
    });
    this.props.dispatch(getcustomerList());
  };

  handleSubmit(hasil) {
    let data = {
      tanggal_daftar: hasil.tanggalDaftar,
      kode_customer: hasil.kodeCustomer,
      nama_customer: hasil.namaCustomer,
      nama_toko: hasil.namaToko,
      kode_ext: hasil.kodeExt,
      kode_provinsi: hasil.kodeProvinsi,
      level_customer: hasil.level_customer,
      kode_customer_induk:
        hasil.kode_customer_induk === undefined
          ? "-"
          : hasil.kode_customer_induk,
      kode_kota: hasil.kodeKota,
      kode_kecamatan: hasil.kodeKecamatan,
      alamat: hasil.alamat,
      kode_pos: hasil.kodePos,
      contact_person: hasil.contactPerson,
      no_telp: hasil.telepon,
      no_hp: hasil.handPhone,
      fax: hasil.fax,
      email: hasil.email,
    };
    let dataEdit = {
      tanggal_daftar: hasil.tanggalDaftar,
      nama_customer: hasil.namaCustomer,
      nama_toko: hasil.namaToko,
      kode_ext: hasil.kodeExt,
      kode_provinsi: hasil.kodeProvinsi,
      level_customer: hasil.level_customer,
      kode_customer_induk:
        hasil.kode_customer_induk === undefined
          ? "-"
          : hasil.kode_customer_induk,
      kode_kota: hasil.kodeKota,
      kode_kecamatan: hasil.kodeKecamatan,
      alamat: hasil.alamat,
      kode_pos: hasil.kodePos,
      contact_person: hasil.contactPerson,
      no_telp: hasil.telepon,
      no_hp: hasil.handPhone,
      fax: hasil.fax,
      email: hasil.email,
    };
    this.props.dispatch(progressSend());
    AxiosMasterPost("customer/add", data)
      .then(() => this.props.dispatch(finishSend()))
      .then(() => this.props.dispatch(hideModal()))
      .then((res) =>
        berhasil("Data Berhasil Ditambah!")
          .then(() => LogData("Data Berhasil Ditambah Di Customer", data))
          .then(() => this.props.dispatch(getcustomerList()))
          .then(() => this.props.dispatch(reset("Customer")))
      )
      .catch((err) =>
        this.handleReactive(
          err,
          hasil.kodeCustomer,
          dataEdit,
          this.props.dispatch(finishSend())
        )
      );
  }

  handleEdit(hasil) {
    let kirim = {
      user_id: hasil.user_id,
      password: hasil.password,
    };
    AxiosMasterPost("users/check", kirim)
      .then(() => {
        let data = {
          tanggal_daftar: hasil.tanggalDaftar,
          nama_customer: hasil.namaCustomer,
          nama_toko: hasil.namaToko,
          kode_ext: hasil.kodeExt,
          level_customer: hasil.level_customer,
          kode_customer_induk:
            hasil.kode_customer_induk === undefined
              ? "-"
              : hasil.kode_customer_induk,
          kode_provinsi: hasil.kodeProvinsi,
          kode_kota: hasil.kodeKota,
          kode_kecamatan: hasil.kodeKecamatan,
          alamat: hasil.alamat,
          kode_pos: hasil.kodePos,
          contact_person: hasil.contactPerson,
          no_telp: hasil.telepon,
          no_hp: hasil.handPhone,
          fax: hasil.fax,
          email: hasil.email,
        };
        this.props.dispatch(progressSend());
        AxiosMasterPut("customer/edit/" + hasil.kodeCustomer, data)
          .then(() => this.props.dispatch(finishSend()))
          .then(() => this.props.dispatch(hideModal()))
          .then((res) =>
            berhasil("Data Berhasil Diubah!")
              .then(() => LogData("Data Berhasil Diubah Di Customer", data))
              .then(() => this.props.dispatch(getcustomerList()))
          )
          .catch((err) =>
            gagal("Data Gagal Diubah!")
              .then(() => this.props.dispatch(finishSend()))
              .then(() => LogData("Data Gagal Diubah Di Customer", data, err))
              .then(() => this.props.dispatch(hideModal()))
          );
      })
      .catch(() => {
        warning("Gagal Verifikasi Account");
        return false;
      });
  }

  tambahModal() {
    this.props.dispatch(showModal());
    this.setState({ isEdit: false });
  }
  editModal() {
    this.props.dispatch(showModal());
    this.setState({
      isEdit: true,
    });
  }
  handleReactive(err, kode, data) {
    this.props.dispatch(hideModal());
    let error = err.response.data;
    let check = error.includes("deleted");
    check
      ? reactive(
          err,
          kode,
          "customer/reactive/",
          data,
          "customer/edit/"
        ).then(() => this.props.dispatch(getcustomerList()))
      : gagal("Data Gagal Ditambahkan");
  }
  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div id="notif_alert" className={this.props.alert} role="alert">
          <button
            type="button"
            className="close"
            data-dismiss="alert"
            aria-label="Close"
          >
            <span aria-hidden="true"></span>
          </button>
          <strong>Error - </strong> Tidak Bisa Mengambil Data
        </div>
        <TourGuide
          tourConfig={[
            `Untuk Menambahkan Data Customer, Tekan Tombol ini Dan Isi kolom yang disediakan`,
            `Ini adalah Data Customer yang sudah tersimpan`,
            `Ini adalah tombol untuk menghapus data pada baris yang dipilih`,
            `Ini adalah tombol untuk mengubah data pada baris yang dipilih`,
          ]}
        />
        <ModalMasterData
          content={
            this.state.isEdit ? (
              <FormCustomerEdit
                onSubmit={(data) => this.handleEdit(data)}
                onSend={this.props.onSend}
              />
            ) : (
              <FormCustomer
                onSubmit={(data) => this.handleSubmit(data)}
                onSend={this.props.onSend}
              />
            )
          }
          title={
            this.state.isEdit ? "Edit Data Customer" : "Tambah Data Customer"
          }
        />
        <div className="row">
          <div className="col-12">
            <div className="card">
              <HeaderCard title="Data Customer" />
              <div className="card-body">
                <form action="#">
                  <div className="form-body">
                    <div className="col-lg-12 col-md-12">
                      <div className="text-right">
                        <button
                          name="simpan"
                          id="simpan"
                          type="button"
                          className="btn btn-info"
                          onClick={() => this.tambahModal()}
                          data-tut="reactour_1"
                        >
                          Tambah Data Customer
                        </button>
                      </div>
                    </div>
                  </div>
                </form>
                <div data-tut="reactour_2">
                  {this.props.customerList ? (
                    <ToolkitProvider
                      keyField="kode_customer"
                      data={this.props.customerList}
                      columns={this.state.columns}
                      search
                    >
                      {(props) => (
                        <div className="text-left mt-5 mb-3">
                          <div className="text-right">
                            <SearchBar
                              {...props.searchProps}
                              className="form-control"
                              placeholder="Pencarian"
                              style={{ margin: 0, top: "50%" }}
                            />
                          </div>
                          <hr />
                          <BootstrapTable
                            {...props.baseProps}
                            striped
                            hover
                            condensed
                            bordered={true}
                            headerClasses="header-table"
                            expandRow={rowExpand}
                            pagination={paginationFactory()}
                          />
                          <ExportCSVButton
                            {...props.csvProps}
                            className="btn btn-primary"
                          >
                            Export CSV
                          </ExportCSVButton>
                        </div>
                      )}
                    </ToolkitProvider>
                  ) : (
                    <div>
                      {this.props.error ? (
                        <div className="text-center">
                          <img
                            src={notrespond}
                            style={{ width: "30%" }}
                            alt="Not Respond"
                          />
                          <br />
                          <h4>Request API Gagal, Silahkan Refresh</h4>
                        </div>
                      ) : (
                        <Skeleton width={"100%"} height={180} />
                      )}
                    </div>
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(maptoStae, null)(Customer);
