const validationCustomer = (value) => {
  const errors = {};
  if (!value.kodeSupplier) {
    errors.kodeSupplier = "Tidak Boleh kosong";
  }
  if (!value.kodeExt) {
    errors.kodeExt = "Tidak Boleh kosong";
  }
  if (!value.namaToko) {
    errors.namaToko = "Tidak Boleh kosong";
  }
  if (!value.namaCustomer) {
    errors.namaCustomer = "Tidak Boleh kosong";
  }
  if (!value.tanggalDaftar) {
    errors.tanggalDaftar = "Tidak Boleh kosong";
  }
  if (!value.kodeArea) {
    errors.kodeArea = "Tidak Boleh kosong";
  }
  if (!value.kodeKota) {
    errors.kodeKota = "Tidak Boleh kosong";
  }
  if (!value.user_id) {
    errors.user_id = "Tidak Boleh kosong";
  }
  if (!value.password) {
    errors.password = "Tidak Boleh kosong";
  }
  return errors;
};

export default validationCustomer;
