import React, { Component } from "react";
import Stepper from "react-stepper-horizontal";
import { Field, reduxForm } from "redux-form";
import { connect } from "react-redux";
import validationCustomer from "../../Validations/validationCustomer";
import { renderField, renderFieldSelect } from "../../renderField";
import { AxiosMasterGet } from "../../Axios";
import { getToday } from "../../getDate";

class FormCustomer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      step: 0,
      step1: "row",
      step2: "row d-none",
      step3: "row d-none",
      customer_induk: "col-lg-5 d-none",
      listKota: [],
      listArea: [],
      listProvinsi: [],
      pilihanArea: [],
      pilihanKota: [],
      list_customer_induk: [],
      today:
        new Date().getFullYear() +
        "-" +
        ("0" + (new Date().getMonth() + 1)).slice(-2) +
        "-" +
        ("0" + new Date().getDate()).slice(-2),
    };
  }

  nextStep = () => {
    const { step } = this.state;
    if (step === 0) {
      this.setState({ step: step + 1, step2: "row", step1: "row d-none" });
    } else if (step === 1) {
      this.setState({ step: step + 1, step2: "row d-none", step3: "row" });
    }
  };

  prevStep = () => {
    const { step } = this.state;
    if (step === 1) {
      this.setState({ step: step - 1, step2: "row d-none", step1: "row" });
    } else if (step === 2) {
      this.setState({ step: step - 1, step3: "row d-none", step2: "row" });
    }
  };

  componentDidMount() {
    AxiosMasterGet("kota/get/all")
      .then((res) =>
        this.setState({
          listKota: res.data,
        })
      )
      .catch((err) => console.log(err));

    AxiosMasterGet("kecamatan/get/all")
      .then((res) =>
        this.setState({
          listArea: res.data,
        })
      )
      .catch((err) => console.log(err));
    AxiosMasterGet("provinsi/get/all")
      .then((res) =>
        this.setState({
          listProvinsi: res.data,
        })
      )
      .catch((err) => console.log(err));
    this.props.change("tanggalDaftar", getToday);
  }

  inputProvinsi = (input) => (e) => {
    var kotaPilihan = this.state.listKota.filter(
      (list) => list.kode_provinsi === e.target.value
    );
    this.setState({
      pilihanKota: kotaPilihan,
    });
  };
  inputKota = (input) => (e) => {
    var areaPilihan = this.state.listArea.filter(
      (list) => list.kode_kota === e.target.value
    );
    this.setState({
      pilihanArea: areaPilihan,
    });
  };
  inputArea = (input) => (e) => {
    var result_explode = e.target.value.split("|");
    this.setState({
      kodeArea: result_explode[0],
      namaArea: result_explode[1],
    });
    this.setKodePos(result_explode[0]);
  };

  setKodePos(data) {
    let param = data;
    let kodepos = this.state.listArea.filter(
      (data) => data.kode_kecamatan === param
    );
    this.props.change("kodePos", kodepos[0].kode_pos);
  }

  setIndukCustomer(data) {
    console.log(data);
    switch (data) {
      case "LEVEL1":
        this.setState({
          customer_induk: "col-lg-5 d-none",
        });
        break;
      case "LEVEL2":
        this.setState({
          customer_induk: "col-lg-5",
          list_customer_induk: this.props.customer.filter(
            (data) => data.level_customer === "LEVEL1"
          ),
        });
        break;
      case "LEVEL3":
        this.setState({
          customer_induk: "col-lg-5",
          list_customer_induk: this.props.customer.filter(
            (data) => data.level_customer === "LEVEL2"
          ),
        });
        break;
      default:
        break;
    }
  }

  render() {
    const { step } = this.state;
    return (
      <div className="col-lg-12 col-md-12">
        <form onSubmit={this.props.handleSubmit}>
          <div className="row">
            <div className="col-lg-12 col-md-12">
              <Stepper
                steps={[
                  { title: "Data Diri" },
                  { title: "Data Lokasi" },
                  { title: "Data Nomor" },
                  // { title: "Data Rekening" },
                ]}
                activeStep={step}
              />
            </div>
            <div className="col-lg-12">
              <div className={this.state.step1}>
                <div className="col-md-4 col-lg-4">
                  <Field
                    name="kodeCustomer"
                    type="text"
                    normalize={(value) => value && value.toUpperCase()}
                    component={renderField}
                    label="Kode Customer"
                  />
                </div>
                <div className="col-md-4 col-lg-4">
                  <Field
                    name="kodeExt"
                    type="text"
                    normalize={(value) => value && value.toUpperCase()}
                    component={renderField}
                    label="Kode Ext"
                  />
                </div>
                <div className="col-md-4 col-lg-4">
                  <Field
                    name="namaToko"
                    type="text"
                    normalize={(value) => value && value.toUpperCase()}
                    component={renderField}
                    label="Nama Toko"
                  />
                </div>
                <div className="col-md-4 col-lg-4">
                  <Field
                    name="namaCustomer"
                    type="text"
                    normalize={(value) => value && value.toUpperCase()}
                    component={renderField}
                    label="Nama Customer"
                  />
                </div>

                <div className="col-md-4 col-lg-4">
                  <Field
                    name="tanggalDaftar"
                    type="date"
                    component={renderField}
                    label="Tanggal Daftar"
                  />
                </div>
                <div className="col-lg-5">
                  <Field
                    name="level_customer"
                    component={renderFieldSelect}
                    options={[
                      {
                        value: "LEVEL1",
                        name: "LEVEL 1",
                      },
                      {
                        value: "LEVEL2",
                        name: "LEVEL 2",
                      },
                      {
                        value: "LEVEL3",
                        name: "LEVEL 3",
                      },
                    ]}
                    type="text"
                    label="Level Customer"
                    placeholder="Masukan Level Customer"
                    onChange={(data) => {
                      this.setIndukCustomer(data);
                    }}
                  />
                </div>
                <div className={this.state.customer_induk}>
                  <Field
                    name="kode_customer_induk"
                    component={renderFieldSelect}
                    options={this.state.list_customer_induk.map((hasil) => {
                      let data = {
                        value: hasil.kode_customer,
                        name: hasil.nama_customer,
                      };
                      return data;
                    })}
                    type="text"
                    label="Customer Induk"
                    placeholder="Masukan Customer Induk"
                  />
                </div>
                <div className="col-lg-12 col-md-12">
                  <div className="text-right">
                    <button
                      id="next_1"
                      name="next_1"
                      className="btn btn-primary"
                      type="button"
                      onClick={() => this.nextStep()}
                    >
                      Next
                    </button>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-lg-12">
              <div className={this.state.step2}>
                <div className="col-md-4 col-lg-4">
                  <label htmlFor="">Provinsi</label>
                  <Field
                    name="kodeProvinsi"
                    type="text"
                    normalize={(value) => value && value.toUpperCase()}
                    className="form-control"
                    component="select"
                    label="Nama Customer"
                    onChange={this.inputProvinsi()}
                  >
                    <option value="DEFAULT | DEFAULT">SILAHKAN PILIH</option>
                    {this.state.listProvinsi.map((kota) => (
                      <option
                        value={kota.kode_provinsi}
                        key={kota.kode_provinsi}
                      >
                        {kota.nama_provinsi}
                      </option>
                    ))}
                  </Field>
                </div>
                <div className="col-md-4 col-lg-4">
                  <label htmlFor="">Kota</label>
                  <Field
                    name="kodeKota"
                    type="text"
                    normalize={(value) => value && value.toUpperCase()}
                    className="form-control"
                    component="select"
                    label="Nama Customer"
                    onChange={this.inputKota()}
                  >
                    <option value="DEFAULT | DEFAULT">SILAHKAN PILIH</option>
                    {this.state.pilihanKota.map((kota) => (
                      <option value={kota.kode_kota} key={kota.kode_kota}>
                        {kota.nama_kota}
                      </option>
                    ))}
                  </Field>
                </div>
                <div className="col-md-4 col-lg-4">
                  <label htmlFor="">Kecamatan</label>
                  <Field
                    name="kodeKecamatan"
                    type="text"
                    normalize={(value) => value && value.toUpperCase()}
                    className="form-control"
                    component="select"
                    label="Nama Customer"
                    onChange={this.inputArea()}
                  >
                    <option value="DEFAULT | DEFAULT">SILAHKAN PILIH</option>
                    {this.state.pilihanArea.map((area) => (
                      <option
                        value={area.kode_kecamatan}
                        key={area.kode_kecamatan}
                      >
                        {area.nama_kecamatan}
                      </option>
                    ))}
                  </Field>
                </div>
                <div className="col-md-4 col-lg-4">
                  <Field
                    name="kodePos"
                    type="number"
                    component={renderField}
                    label="Kode Pos"
                  />
                </div>
                <div className="col-md-6 col-lg-6">
                  <Field
                    name="alamat"
                    type="text"
                    normalize={(value) => value && value.toUpperCase()}
                    component={renderField}
                    label="Alamat"
                  />
                </div>
                <div className="col-lg-12 col-md-12">
                  <div className="row">
                    <div className="text-left col-md-6">
                      <button
                        id="back_1"
                        name="back_1"
                        className="btn btn-primary"
                        type="button"
                        onClick={() => this.prevStep()}
                      >
                        Back
                      </button>
                    </div>
                    <div className="text-right col-md-6">
                      <button
                        id="next_2"
                        name="next_2"
                        className="btn btn-primary"
                        type="button"
                        onClick={() => this.nextStep()}
                      >
                        Next
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-lg-12">
              <div className={this.state.step3}>
                <div className="col-md-4 col-lg-4">
                  <Field
                    name="email"
                    type="email"
                    component={renderField}
                    label="Email"
                  />
                </div>
                <div className="col-md-4 col-lg-4">
                  <Field
                    name="telepon"
                    type="number"
                    component={renderField}
                    label="Nomor Telepon"
                  />
                </div>
                <div className="col-md-4 col-lg-4">
                  <Field
                    name="contactPerson"
                    type="number"
                    component={renderField}
                    label="Contact Person"
                  />
                </div>
                <div className="col-md-4 col-lg-4">
                  <Field
                    name="fax"
                    type="number"
                    component={renderField}
                    label="Fax"
                  />
                </div>
                <div className="col-md-4 col-lg-4">
                  <Field
                    name="handPhone"
                    type="number"
                    component={renderField}
                    label="Handphone"
                  />
                </div>
                <div className="col-lg-12 col-md-12">
                  <div className="row">
                    <div className="text-left col-md-6">
                      <button
                        id="back_2"
                        name="back_2"
                        className="btn btn-primary"
                        type="button"
                        onClick={() => this.prevStep()}
                      >
                        Back
                      </button>
                    </div>
                    <div className="text-right col-md-6">
                      <button
                        type="submit"
                        disabled={this.props.onSend}
                        className="btn btn-primary"
                        onClick={() =>
                          this.setState({
                            step: 0,
                            step1: "row",
                            step2: "row d-none",
                            step3: "row d-none",
                          })
                        }
                      >
                        {this.props.onSend ? (
                          <span
                            className="spinner-border spinner-border-sm mr-3"
                            aria-hidden="true"
                          ></span>
                        ) : null}
                        Simpan
                        <i className="fa fa-paper-plane ml-3"></i>
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </form>
      </div>
    );
  }
}

FormCustomer = reduxForm({
  form: "Customer",
  validate: validationCustomer,
  enableReinitialize: true,
})(FormCustomer);
export default connect((state) => {
  return {
    customer: state.provinsi.customer,
  };
})(FormCustomer);
